.login-child {
  position: absolute;
  top: 186px;
  left: 492px;
  border-radius: var(--br-31xl);
  width: 455px;
  height: 667px;
  object-fit: cover;
}
.container1,
.container2 {
  border: 0;
  background-color: rgba(49, 145, 232, 0.42);
  position: absolute;
  top: 456px;
  left: 593px;
  width: 258px;
  height: 63px;
}
.container2 {
  background-color: rgba(181, 0, 127, 0.42);
  top: 622px;
}
.password,
.username {
  position: absolute;
  top: 384px;
  left: 619px;
  font-weight: 900;
}
.password {
  top: 543px;
}
.bot-logo-icon {
  position: absolute;
  top: 260px;
  left: 670px;
  width: 100px;
  height: 100px;
  object-fit: cover;
}
.login1 {
  position: relative;
  font-size: var(--font-size-16xl);
  font-weight: 500;
  font-family: var(--font-inter);
  color: #1e1e1e;
  text-align: left;
}
.logininput {
  border: 0;
  padding: var(--padding-3xs);
  background-color: rgba(255, 65, 198, 0.42);
  position: absolute;
  top: 715px;
  left: 669px;
  border-radius: 30px;
  width: 105px;
  height: 40px;
  display: flex;
  flex-direction: row;
  box-sizing: border-box;
  align-items: center;
  justify-content: center;
  cursor: pointer;
}
.logininput:hover {
  background-color: rgba(255, 195, 237, 0.42);
}
.login {
  position: relative;
  width: 100%;
  height: 1024px;
  overflow: hidden;
  background-image: url(./public/login@3x.png);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: top;
  text-align: left;
  font-size: var(--font-size-21xl);
  color: var(--color-white);
  font-family: var(--font-inter);
}
@media screen and (max-width: 1200px) {
  .logininput:hover {
    background-color: rgba(177, 69, 145, 0.42);
  }
}
